article.society-article {
  width: clamp(20rem, 89%, 110.2rem);
  margin: 0 auto;
  margin-top: 4.5rem;
  background-color: var(--primary-white-color);
}

div.society-header {
  display: grid;
  gap: 2rem;
  place-items: center;
}

img.society-image {
  width: clamp(9.9rem, 22vw, 22rem);
  height: clamp(9.9rem, 22vw, 22rem);
  border-radius: 50%;
}

div.society-text-container {
  text-align: center;
}

.society-text-container h2 {
  font-size: clamp(var(--m-medium-font-title), 2.3vw, 3.8rem);
  font-weight: var(--small-fw);
  line-height: clamp(var(--m-normal-line-height), 2vw, 4.632rem);
}

.society-text-container p {
  font-size: clamp(var(--m-small-text-size), 1.6vw, 1.8rem);
  font-weight: var(--ex-small-fw);
  line-height: clamp(var(--m-ex-small-line-height), 2vw, 3.169rem);
}

div.society-support-container {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr));
  align-items: center;
  gap: 3.9rem;
}
/* 
div.society-support {
  width: clamp(20rem, 24vw, 33.7rem);
  height: clamp(20rem, 22vw, 28.7rem);
} */

img.support-image {
  width: 100%;
  height: 100%;
}

div.support-info-container {
  text-align: center;
}

.support-info-container h3 {
  font-size: clamp(var(--m-small-text-size), 1.6vw, 3rem);
  font-weight: var(--small-fw);
  line-height: clamp(var(--m-small-line-height), 2vw, 3.657rem);
}

.support-info-container p {
  font-size: clamp(var(--m-ex-small-text-size), 1.4vw, 2rem);
  font-weight: var(--ex-small-fw);
  line-height: clamp(var(--m-ex-small-line-height), 2vw, 2.438rem);
}

@media (min-width: 618px) {
  div.society-support-container {
    margin-top: 7rem;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  }
}

@media (min-width: 1000px) {
  div.society-support-container {
    margin-top: 7rem;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  }
}
