article.post-card {
  width: clamp(20rem, 98.7%, 79.6rem);
  margin: 0 auto;
  border: 0.1rem solid var(--tertiary-dark);
  border-radius: 2rem;
  margin-top: 1.5rem;
}

div.post-wrapper {
  margin: auto;
  margin-top: 0.7rem;
  display: grid;
  gap: 2rem;
}

div.post-head {
  display: flex;
  justify-content: space-between;
  width: clamp(20rem, 90%, 75.2rem);
  margin: auto;
}

div.user-details {
  display: grid;
  width: clamp(11.6rem, 22.7rem);
  grid-template-columns: 0.5fr 1fr;
  gap: 1rem;
  align-items: center;
}

div.name-date-container h3 {
  font-size: clamp(var(--m-tertiary-font), 1.8vw, 2.2rem);
  font-weight: var(--small-fw);
  line-height: clamp(2.184, 2vw, 26.82rem);
}

div.name-date-container small {
  font-size: clamp(var(--m-small-text-size), 1.5vw, 1.6rem);
  font-family: var(--ex-small-fw);
  line-height: clamp(1.7rem, 1.5vw, 1.95rem);
}

div.user-image-container {
  width: 100%;
  height: clamp(5.2rem, 3vw, 9.1rem);
}

img.user-avatar {
  width: 100%;
  height: 100%;
}

div.post-menu-container {
  position: relative;
  height: 2.1rem;
}

img.post-menu {
  height: 100%;
}

div.post-content-container {
  width: clamp(20rem, 90%, 75.2rem);
  margin: auto;
  /* margin-top: 2rem; */
}

p.post-content {
  font-size: clamp(var(--m-small-text-size), 1.5vw, 1.6rem);
  font-family: var(--ex-small-fw);
  line-height: clamp(1.7rem, 1.5vw, 1.95rem);
}

div.post-interactions-container {
  border-top: 0.1rem solid var(--tertiary-dark);
}

div.post-btn-wrapper {
  display: flex;
  justify-content: space-around;
  max-width: 35.4rem;
}

.post-interactions-container button {
  width: clamp(2rem, 2vw, 2.2rem);
  /* height: clamp(2rem, 2vw, 2.2rem); */
  margin-top: 2.1rem;
  background-color: transparent;
}

