section.onboarding-section {
  width: clamp(28rem, 100%, 144rem);
  margin: 0 auto;
}

div.onboarding-wrapper {
  width: clamp(20rem, 89%, 110.2rem);
  margin: 0 auto;
}

div.onboarding-steps-container {
  width: clamp(20rem, 100%, 144rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

div.onboarding-steps {
  display: grid;
  gap: 0.7rem;
  grid-template-columns: repeat(3, 1fr);
}

.onboarding-steps p {
  font-size: clamp(var(--m-medium-text-size), 2.5vw, 3rem);
  background-color: #b2b2b2;
  width: clamp(3rem, 3vw, 6.1rem);
  height: clamp(3rem, 3vw, 6.1rem);
  border-radius: 50%;
  text-align: center;
}

p.current-step {
  color: var(--primary-white-color);
  background-color: var(--light-blue);
}

button.onboarding-btn {
  font-size: clamp(var(--m-small-text-size), 2vw, 2.8rem);
  font-weight: var(--normal-fw);
  line-height: clamp(var(--m-ex-small-line-height), 2.3vw, 3.413rem);
  text-align: left;
  background: transparent;
  color: var(--tertiary-dark);
}

div.next-onboarding-step {
  margin-top: 6.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.next-onboarding-step button {
  background-color: transparent;
  border: 0.1rem solid var(--tertiary-dark);
  border-radius: 0.5rem;
  padding: 0.3rem 1rem;
  font-family: Montserrat;
  font-size: clamp(var(--m-small-text-size), 1.6vw, 2.5rem);
  font-weight: var(--small-fw);
  line-height: clamp(var(--m-ex-small-line-height), 1.9vw, 3rem);
  text-align: center;
}

@media (min-width: 618px) {
  section.onboarding-section {
    width: clamp(28rem, 100%, 144rem);
    margin: 0 auto;
  }

  div.onboarding-wrapper {
    width: clamp(20rem, 89%, 110.2rem);
    margin: 0 auto;
    border-radius: 2rem;
    padding: 2rem;
  }
}
