article.login-container {
  min-height: 81.2rem;
  max-height: 96.7rem;
  position: relative;
  width: clamp(28rem, 100%, 81.5rem);
  width: clamp(28rem, 100%, 144rem);
}

div.login-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
  gap: 4.1rem;
  margin-top: 6.6rem;
  /* justify-content: center; */
}

div.login-image-container {
  display: grid;
  justify-items: center;
}

img.login-image {
  width: clamp(24.7rem, 65.8%, 58.9rem);
  min-height: 19.6rem;
  max-height: 43.958rem;
  /* margin: 0 auto; */
}

div.login-brand-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

img.login-brand-logo {
  width: clamp(20rem, 21.1%, 17.2rem);
  min-height: 10.1rem;
  max-height: 96.7rem;
}

form.login-form {
  display: grid;
  gap: 2rem;
  width: clamp(20rem, 89.6%, 50.8rem);
  margin: 0 auto;
}

.login-form div.input-container {
  display: grid;
  gap: 2rem;
  width: 100%;
}

.login-form h3 {
  color: var(--light-blue);
  font-size: var(--m-big-font-title);
  font-weight: var(--small-fw);
  line-height: var(--m-big-line-height);
}

.login-form input {
  width: 100%;
  border: 0.1rem solid var(--tertiary-dark);
  border-radius: 1.6rem;
  min-height: 5.3rem;
  max-height: 7rem;
  padding: 1.5rem;
}

.login-form input::placeholder {
  color: rgba(0, 0, 0, 0.8);
  font-size: clamp(var(--m-small-text-size), 1.5vw, 1.6rem);
  font-weight: var(--ex-small-fw);
}

.login-form button {
  min-height: 5.3rem;
  max-height: 7rem;
  width: 100%;
  padding: 1.5rem;
  border-radius: 1.6rem;
  color: var(--primary-white-color);
  background-color: var(--light-blue);
  font-size: var(--m-medium-text-size);
  font-weight: var(--small-fw);
}

div.new-user-call-to-action-container {
  display: grid;
  place-items: center;
  gap: 2.3rem;
  width: clamp(20rem, 89.6%, 50.8rem);
  margin: 0 auto;
  margin-top: 2.3rem;
}

p.call-to-action-text {
  font-size: var(--m-medium-text-size);
  font-weight: var(--ex-small-fw);
  line-height: 2.5rem;
  text-align: center;
}

.call-to-action-text a {
  color: var(--light-blue);
  margin-left: 1.7rem;
}

/* Background login-icons */

div.login-bg-icons-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

img.login-bg-icon {
  position: inherit;
}

img.login-bg-icon:first-of-type {
  top: 3.3rem;
  right: 0;
}

img.login-bg-icon:nth-child(2) {
  top: 3.3rem;
  left: 0;
}

img.login-bg-icon:last-of-type {
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
}

@media (min-width: 618px) {
  div.login-wrapper {
    margin-top: unset;
  }

  div.login-image-container {
    background-color: #827f54;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .login-image-container h3 {
    font-weight: var(--small-fw);
    line-height: 4.6rem;
    color: var(--primary-white-color);
    font-size: clamp(2.2rem, 2.3vw, 3.2rem);
    /* text-align: center; */
  }

  .login-image-container p {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.5rem;
    text-align: center;
    color: var(--primary-white-color);
  }

  img.login-image {
    align-self: center;
  }

  div.login-content-container {
    display: grid;
    align-items: center;
    min-height: 81.2rem;
    max-height: 96.7rem;
  }

  div.login-brand-container {
    width: 17.2rem;
    height: 17.2rem;
    margin: 0 auto;
  }
}
