section.profile-feed {
  width: clamp(28rem, 100%, 80.6rem);
}

div.profile-engagement-container {
  border-bottom: 0.1rem solid #000000b2;
}

ul.profile-engagement-list {
  display: flex;
  justify-content: space-between;
  width: clamp(20rem, 85.8%, 65.1rem);
  margin: 1.7rem;
}

li.engagement-item {
  font-size: clamp(1.2rem, 1.5vw, 2.2rem);
  font-weight: var(--ex-small-fw);
  line-height: clamp(1.463rem, 1.4vw, 2.682rem);
  position: relative;
  cursor: pointer;
}

li.engagement-item::after {
  content: "";
  position: absolute;
  width: 0;
  left: 0;
  right: 0;
  bottom: -0.7rem;
  height: 0.1rem;
  background-color: var(--tertiary-dark);
  transition: width,
    var(--normal-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

li.engagement-item:hover::after {
  width: 100%;
}
