article.otp-container {
  min-height: 81.2rem;
  max-height: 96.7rem;
  position: relative;
  width: clamp(28rem, 100%, 81.5rem);
  width: clamp(28rem, 100%, 144rem);
}

div.otp-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
  gap: 4.1rem;
  margin-top: 6.6rem;
  /* justify-content: center; */
}

div.otp-image-container {
  display: grid;
  gap: unset;
  justify-items: center;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

img.otp-image {
  width: clamp(24.7rem, 65.8%, 58.9rem);
  min-height: 19.6rem;
  max-height: 43.958rem;
  /* margin: 0 auto; */
}

.otp-form {
  display: grid;
  gap: 2rem;
  width: clamp(20rem, 89.6%, 50.8rem);
  margin: 0 auto;
}

.otp-form div.input-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  /* width: 20rem; */
  /* margin: 0 auto; */
}

div.otp-text-container h3 {
  font-size: clamp(
    var(--m-medium-font-title),
    var(--m-medium-font-title),
    3.2rem
  );
  font-weight: var(--small-fw);
  line-height: clamp(var(--m-normal-line-height), 2vw, 3.9rem);
  text-align: center;
}

div.otp-text-container p {
  font-size: var(--m-medium-text-size);
  font-weight: var(--ex-small-fw);
  line-height: 2rem;
  text-align: center;
}

.otp-text-container p span {
  color: var(--light-blue);
}

.otp-form input {
  width: clamp(2rem, 4vw, 7rem);
  height: clamp(2rem, 4vw, 7rem);
  border: 0.1rem solid var(--tertiary-dark);
  border-radius: 0.7rem;
  padding: 1rem;
}

.otp-form button {
  min-height: 5.3rem;
  max-height: 7rem;
  width: 100%;
  padding: 1.5rem;
  border-radius: 1.6rem;
  color: var(--primary-white-color);
  background-color: var(--light-blue);
  font-size: var(--m-medium-text-size);
  font-weight: var(--small-fw);
}

div.otp-resend-link-container {
  display: grid;
  place-items: center;
  gap: 2.3rem;
  width: clamp(20rem, 89.6%, 50.8rem);
  margin: 0 auto;
  margin-top: 2.3rem;
}

p.otp-resend-link {
  font-size: var(--m-medium-text-size);
  font-weight: var(--ex-small-fw);
  line-height: 2.5rem;
  text-align: center;
}

.otp-resend-link a {
  color: var(--light-blue);
  margin-left: 1.7rem;
}

@media (min-width: 618px) {
  div.otp-wrapper {
    margin-top: unset;
    min-height: 81.2rem;
    max-height: 96.7rem;
  }

  div.otp-image-container {
    gap: unset;
    background-color: #827f54;
  }

  .otp-image-container h3 {
    font-weight: var(--small-fw);
    line-height: 4.6rem;
    color: var(--primary-white-color);
    font-size: clamp(2.2rem, 2.3vw, 3.2rem);
    /* text-align: center; */
  }

  .otp-image-container p {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.5rem;
    text-align: center;
    color: var(--primary-white-color);
  }

  img.otp-image {
    align-self: center;
  }

  div.otp-content-container {
    display: grid;
    align-items: center;
    /* height: 100vh; */
  }

  div.otp-brand-container {
    width: 17.2rem;
    height: 17.2rem;
    margin: 0 auto;
  }

  img.otp-brand-img {
    width: 100%;
    height: 100%;
  }

  /* .otp-form div:only-of-type {
    justify-self: center;
  } */
}
