aside.user-option-container {
  position: absolute;
  right: 0;
  top: 2rem;
  width: 37.5rem;
  width: 18.3rem;
  height: 21.7rem;
  border-radius: 2rem 2rem 4rem 4rem;
  background-color: #fff;
  border: 0.1rem solid black;
  padding: 18px 166px 17px 26px;
  font-size: var(--m-tertiary-font);
  font-size: 1.6rem;
  font-weight: var(--small-fw);
  line-height: normal;
  z-index: 1;
}

ul.option-list {
  display: grid;
  gap: 1.8rem;
  width: 18.3rem;
  height: 18.2rem;
  overflow: hidden;
  /* margin-top: 1.8rem;
  margin-left: 2.6rem; */
}
